.myPosts {
    margin-top: 10px;
    background-color: white;
    border-radius: 5px;
}
.wrap {
    padding: 5px 0px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;

}

.textarea {
    border-radius: 5px;
    border: 2px solid brown;
    height: 100%;
    width: 80%;
}

.button {
    border-radius: 5px;
    height: 100%;
    width: 18%;
    background-color: brown;
    font-size: 18px;
    font-weight: 500;
    color: white;
}

